@font-face {
  font-family: SecretLifeofPets;
  src: url("LifeOfPets-Mlrp.9090059b.ttf") format("truetype");
}

.cta-text {
  color: #fff;
  text-align: center;
  margin: 20px auto;
  font-family: SecretLifeofPets;
  font-size: 60px;
}

.hidden {
  display: none;
}

body {
  background-color: #6486a0;
}

.header {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  padding: 30px;
  display: flex;
}

.header img {
  width: 210px;
  height: 180px;
}

.filters {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.filters button {
  color: #fff;
  cursor: pointer;
  background-color: #e53935;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: all .3s;
}

.filters button:hover {
  color: #e53935;
  background-color: #fff;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px #0003;
}

.pets-container {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  display: flex;
}

.pet-card {
  text-align: center;
  background-color: #fff;
  border: 3px solid #6486a0;
  border-radius: 12px;
  flex-direction: column;
  justify-content: space-between;
  width: 250px;
  margin: 20px;
  transition: all .3s;
  display: flex;
  box-shadow: 0 8px 20px #00000026;
}

.pet-card:hover {
  background-color: #f9fbfd;
  border-color: #4a6c85;
  transform: translateY(-5px)scale(1.03);
  box-shadow: 0 16px 35px #00000059;
}

.pet-card h2 {
  color: #e53935;
  margin-bottom: 10px;
  font-family: Luckiest Guy, cursive;
  font-size: 1.5rem;
}

.pet-card img {
  -o-object-fit: cover;
  object-fit: cover;
  cursor: pointer;
  border-radius: 8px;
  width: 100%;
  height: 250px;
  margin-bottom: 15px;
}

.pet-card p {
  color: #333;
  margin: 5px 0;
  font-family: Arial, sans-serif;
  font-size: .95rem;
}

.popup-card {
  text-align: center;
  z-index: 9999;
  background: #fff;
  border-radius: 20px;
  width: 90%;
  max-width: 450px;
  padding: 25px;
  animation: .4s popup-fade;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 15px 30px #0006;
}

.popup-card h2 {
  color: #e53935;
  letter-spacing: 1px;
  margin-bottom: 15px;
  font-family: Luckiest Guy, cursive;
  font-size: 1.8rem;
}

.popup-card img {
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 12px;
  width: 100%;
  height: 300px;
  margin-bottom: 20px;
  box-shadow: 0 5px 15px #0003;
}

.popup-card p {
  color: #444;
  margin-bottom: 10px;
  font-family: Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

.close-btn {
  color: #fff;
  cursor: pointer;
  background: #e53935;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  font-size: 20px;
  transition: background .3s;
  display: flex;
  position: absolute;
  top: -10px;
  right: -10px;
  box-shadow: 0 2px 6px #0000004d;
}

.close-btn:hover {
  background: #c62828;
}

@keyframes popup-fade {
  from {
    opacity: 0;
    transform: translate(-50%, -50%)scale(.9);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%)scale(1);
  }
}
